.Introbody{
    background-color: #F8F8F8;
}

.Circle {
    display: flex;
    justify-content: center;
    align-self: center;
    margin-bottom: 100px;
}

.container2{
    // width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 16px;
}

.title{
    font-weight: 700;
    font-size: 40px;
    line-height: 110%;
    text-align: left;
    width: 100%;
}

.dotsWrap{
    display: flex;
    width: 100%;
    gap: 12px;
    text-align: left    ;
    margin : 40px 0 55px 0
}

.wrap{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.dot{
    background-color: #4229DC80;
    width: 10px;
    height: 10px;
    border-radius: 50%;
}
.active{
    background: #4229DC;
    border-radius: 50px;
    width: 32px;
}

.btn{
    background-color: #4229DC;
    width: 100%;
    border-radius: 20px;
    padding: 14px 32px 14px 32px;
    color :#FFFFFF;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    border: none;
    box-shadow: 0px 2px 12px #4229DC;
    outline: none;
}

.desc {
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    text-align: left;
}

* h1 p h2 h3{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 110%;
}

.globe {
    border: 2px solid #4229DC;
    border-radius: 50%;
    height: 170px;
    overflow: hidden;
    position: absolute;
    width: 170px;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    display: block;
    margin-top: 37px;
    .earth_round {
      animation: rotation 5s linear 0s infinite;
      
      path {
        stroke: #4229DC;
      }
    }
  }
  
  @keyframes rotation {
    from {
      transform: translateX(0);
    }
    
    from {
      transform: translateX(-50%);
    }
  }

.planet{
    display: flex;
}